/**
 * 用户信息相关store
 */

import request from "@/utils/request";

const user = {
  namespaced: true,
  state: {
    // 登录信息
    loginInfo: {
      token: null,
      uid: null,
    },
    // 当前账号绑定的各项ID信息
    accountIdInfo: {
      aid: null,
      change_date: null,
      date: null,
      id: null,
      iid: null,
      is_admin: null,
      mtids: null,
      uid: null,
    },
  },
  mutations: {
    // 设置登录信息
    setLoginInfo(state, loginInfo) {
      state.loginInfo = loginInfo;
    },
    // 设置当前账号绑定的各项ID信息
    setAccountIdInfo(state, accountIdInfo) {
      state.accountIdInfo = accountIdInfo;
    },
  },
  actions: {
    async getLoginInfo({ commit }, params) {
      const res = await request("login.userAuthPhone", params);
      commit("setLoginInfo", res);
    },
    /**
     * 获取当前账号绑定的各项ID信息
     */
    async getAccountIdInfo({ commit }) {
      const res = await request("association.reqMemberMe");
      commit("setAccountIdInfo", res);
    },
  },
};

export default user;
