/**
 * 讲师信息相关store
 */

import request from "@/utils/request";

const lecturer = {
  namespaced: true,
  state: {
    lecturerInfo: {},
  },
  mutations: {
    setLecturerInfo(state, lecturerInfo) {
      state.lecturerInfo = lecturerInfo;
    },
  },
  actions: {
    getLecturerInfo({ commit, dispatch, state }) {
      return new Promise(async (resolve, reject) => {
        let lecturerInfo = await request("instructor.instructorMyInfo");

        if (lecturerInfo) {
          commit("setLecturerInfo", lecturerInfo);
          resolve(lecturerInfo);
        } else {
          reject();
        }
      });
    },
  },
};

export default lecturer;
